@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

.app {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100%;
  color: #2d3133;
}

.grid-item {
  display: grid;

  padding: 2.4rem 4.8rem;
  gap: 2.4rem;
  border: 1.5px solid #2d3133;
  border-radius: 8px;
  font-size: 2.4rem;
}

.grid-item p {
  justify-self: center;
}

.input-range {
  display: flex;
  gap: 1.6rem;
}

.input-range input {
  flex: 1;
}

.input-range span {
  min-width: 10rem;
  text-align: right;
  /* display: flex; */
}
.input-number {
  display: flex;
  gap: 2.4rem;
}

.btn {
  padding: 1.2rem 2.4rem;
  background-color: transparent;
  font-size: inherit;
  border-radius: 8px;
  font-family: inherit;
  border: none;
  border: 1px solid #2d3133;
  cursor: pointer;
  /* transition: all 0.1s ease-in; */
}

.btn:hover {
  color: #fff;
  background-color: rgba(45, 49, 51, 0.9);
}

.btn:focus,
.btn:active {
  outline-color: transparent;
  outline-style: solid;
  background-color: #2d3133;
  color: #fff;
  box-shadow: 0 0 0 2px #2d3133;
}

.input-number input {
  display: block;
  padding: 1.2rem 2.4rem;
  font-size: inherit;
  border-radius: 8px;
  border: none;
  border: 1px solid #2d3133;
  color: #2d3133;
}

.input-number input:focus {
  outline-color: transparent;
  outline-style: solid;
  background-color: #2d3133;
  box-shadow: 0 0 0 2px #2d3133;
  color: #fff;
}
